body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.search-box {
  position: relative;
  height: 300px;
  margin: 10px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  text-align: left;
  font-size: 12px;
  font-weight: 500;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: 4px 2px;
}

#timedate {
  position: absolute;
  top: 15px;
  left: 20px;
  text-shadow: 1px 1px;
}

#timecat {
  position: absolute;
  top: 15px;
  left: 250px;
  text-shadow: 1px 1px;
}

#position-lat, #position-lon {
  position: absolute;
  height: 20px;
  width: 100px;
  border: 1px solid black;
  border-radius: 2px;
  box-shadow: 4px 2px;
}

#position-lat {
  top: 50px;
  left: 25px;
}

#position-lon {
  top: 50px;
  left: 140px;
}

.button-search {
    position: absolute;
    top: 34px;
    left: 262px;
    height: 35px;
    width: 100px;
    margin: 10px 0 0 0px;
    padding: 0px 0px 0px 0px;
}
.button-search:hover {
    margin: 10px 0 0 0px;
    width: 110px;
}

#minDistance {
  position: absolute;
  top : 80px;
  left: 25px;
  height: 20px;
}

#start-station,
#end-station,
#start-station-id,
#end-station_id {
  position: absolute;
  left: 30px;
  border: 1px solid black;
  border-radius: 2px;
  box-shadow: 4px 2px lightgray;
}
#start-station-id {
  top: 115px;
  width: 40px;
  height: 30px;
  z-index:5;
}

#start-station {
  top: 115px;
  width: 345px;
  height: 30px;
  z-index:2;
}

#end-station-id {
  top: 160px;
  width: 40px;
  height: 30px;
  z-index: 50;
}

#end-station {
  top: 160px;
  width: 345px;
  height: 30px;
  z-index:2;
}

#radio-box-time,
#radio-box-profile {
  position: absolute;
  top: 205px;
  width: 150px;
  border: 1px solid black;
}
#radio-box-time {
  left: 35px;
}
#radio-box-profile {
  left: 220px;
}


#map {
  height: 800px;
  width: 100%;
  margin: 0px 0px 0px 0px;
  text-align: center;
  border: 1 px solid black;
}

/* Always set the map height explicitly to define the size of the div
  * element that contains the map. */



.AppBar {
  text-align: center;
}

.AppBar-header {
  position: relative;
  background-image: url("https://media.giphy.com/media/o0mdBMezyYk5q/giphy.gif");
  background-color: #222;
  height: 145px;
  padding: 20px;
  color: white;
}

#nameBox {
    position: absolute;
    top: 0px;
    left: 50px;
    width: 250px;
    height: 100px;
    padding: 15px;
    margin: 20px 0 0 0;
    text-align: center;
    background: yellow;
    color: black;
    border-radius: 100px;
    border: solid 1px black;
}

.AppBar-title {
  position: absolute;
  top: 50px;
  left: 50%;
  color: white;
  font-size: 4rem;
}

.info-card {
  height: 280px;
  width: 105%;
  margin: 10px 0 0 0;
  border-radius: 5px;
  border: solid 1px black;
  box-shadow: 2px 4px lightgray;
}

